const Error = () => {
  return (
    <div className={styles['block']}>
      <img src={error} alt="" />
      <h1>  </h1>
      <p>,  ,     -  . <span>     </span></p>
      <NavLink to='/'>
        <ButtonTwo>
           
        </ButtonTwo>
      </NavLink>
    </div>
  )
}

export default Error